"use server";

import { db } from '@/lib/firebase';
import { collection, addDoc, doc, setDoc, serverTimestamp, Timestamp } from 'firebase/firestore';
import { revalidatePath } from 'next/cache';

export type DocType = 'markdown' | 'html';

export interface DocumentData {
  content: string;
  type: DocType;
  createdAt?: Timestamp;
  updatedAt?: Timestamp;
}

export async function saveDocument(id: string | null, content: string, type: DocType) {
  try {
    if (id) {
      const docRef = doc(db, 'documents', id);
      await setDoc(docRef, { content, type, updatedAt: serverTimestamp() }, { merge: true });
      revalidatePath(`/${id}`);
      revalidatePath(`/view/${id}`);
      return { id };
    } else {
      const docRef = await addDoc(collection(db, 'documents'), {
        content,
        type,
        createdAt: serverTimestamp(),
        updatedAt: serverTimestamp(),
      });
      return { id: docRef.id };
    }
  } catch (error) {
    console.error("Error saving document: ", error);
    let message = 'Unknown Error';
    if (error instanceof Error) message = error.message;
    return { error: `Failed to save document: ${message}` };
  }
}
